body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
}

.App {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  color: #555;
}

h1, h2, h3, h4, h5 {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  color: #111;
  margin-top: 15px
}

p {
  margin-top: 10px;
}

.paragraph-sub {
  font-size: 12px;
  font-style: italic;
}

li {
  list-style-position: inside;
}

blockquote {
  background-color: rgb(248, 248, 248);
  border-left: 2px solid rgb(202, 202, 202);
  font-style: italic;
  padding: 5px 0px 5px 15px;  
}

pre {
  background-color: rgb(248, 248, 248);
  border-left: 2px solid rgb(202, 202, 202);
  padding: 5px 0px 5px 15px;  
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}

.line-break {
  width: 100%;
  height: 1px;
  background-color: rgb(105, 105, 105);
  margin-top: 10px;
}

.three-viewer {
  border: none;
  width: 100%;
  height: 500px;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .container {

  }
  .product-image {

  }
}

@media (min-width: 768px) {
  .container {
    width: 700px;
  }  
}

@media (min-width: 1200px) {
  .container {
    width: 1100px;
  }  
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.product-image {
  width: 100%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'Header';
  src: url(https://images.holoscope.io/Web/Fonts/Header-Thin.ttf);
}

header {
  background-color: #111;
}

.title {
  font-family: 'Galada', cursive;
    font-size: 40px;
  color: white;
  margin: 0px auto;

}

.title-small {
  font-family: 'Galada', cursive;
  font-size: 40px;
  color: white;
  margin: 0px auto;

}

.hamburger-menu {
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

@media (max-width: 768px) {
  .title {
    display: none;
  }
}

@media (min-width: 768px) {
  .hamburger-menu {
    display: none;
  }
  .title-small {
    display: none;
  }

}


